.categories{
	min-width: 250px;
	background-color: rgba(46, 45, 45, 0.829);
	color: #fff;
	padding-left: 40px;
}
.categories-menu {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	max-width: 250px;
	list-style: none;
}

.categories-menu li {
  height: 50px;
  transition: background-position-x 0.9s linear;
  text-align: center;

}

.categories-menu li > a {
  display: block;
  height: 50px;
  font-size: 22px;
  color: #777;
  text-decoration: none;
  transition: all 0.45s;
}

.categories-menu li:hover {
  background: url('img/line.svg');
  animation: line 1s;
  height: 50px;
  cursor: pointer;
}

.categories-menu li:hover a {
  color: #d94f5c;
}

@keyframes line {
  0% {
    background-position-x: 390px;
  }
}
