.film-page {
	position: fixed;
	top: 0;
	width: 100vw;
	height: 100vh;
}

.film-about {
	position: relative;
}

.left-block {
	position: absolute;
	top: 0;
	left: -35vw;
	width: 35vw;
	height: 100vh;
	background: #ccc;
}


.right-block {
	position: absolute;
	top: 0;
	right: 0;
	width: 65vw;
	height: 100vh;
	background: #fff;
}